import { Grid, Typography } from "@mui/material";
import React, { Component } from "react";

export default class AboutUs extends Component {
  constructor(props) {
    super(props);
    this.state = { smallPadding: false };
    window.addEventListener("resize", this.checkScreenWidth);
  }

  checkScreenWidth = () => {
    if (window.innerWidth < 600) {
      this.setState({ smallPadding: true });
    } else {
      this.setState({ smallPadding: false });
    }
  };

  render() {
    return (
      <React.Fragment>
        <Grid
          spacing={0}
          container
          sx={{
            width: "100vw",
            height: "auto",
            paddingTop: this.state.smallPadding ? "10vw" : "50px",
            paddingBottom: this.state.smallPadding ? "20vw" : "150px",
            minWidth: "130px",
            backgroundImage: `linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4)), url('./light_blue_gradient_bg.png')`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            // background: "white",
          }}
          justifyContent="center" // horizontally center
          alignItems="center" // vertically center
        >
          <Grid item style={{ width: "75vw" }}>
            <Typography
              textAlign="center"
              sx={{
                fontSize: "min(5vw, 35px)",
                color: "black",
                fontWeight: "bold",
                margin: "15px",
              }}
            >
              About Us
            </Typography>
            <Typography
              style={{ textAlign: "justify" }}
              sx={{ fontSize: "min(3vw, 30px)", color: "black" }}
            >
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
              Architecto atque dignissimos ab nobis, sint perferendis eveniet
              eius est sed adipisci harum delectus quidem! Dolorem sint
              voluptates est placeat quia vel, dignissimos doloremque accusamus
              in. Eaque ab hic dolore quibusdam id. Architecto culpa sunt soluta
              quas expedita ratione impedit libero omnis!
            </Typography>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {}
  componentDidCatch(error, info) {}
  componentWillUnmount() {}
}
