import React, { Component } from "react";
import AppBar from "@mui/material/AppBar";

import MenuIcon from "@mui/icons-material/Menu";
import InfoIcon from "@mui/icons-material/Info";
import HomeIcon from "@mui/icons-material/Home";
import ComputerIcon from "@mui/icons-material/Computer";
import ContactMailIcon from "@mui/icons-material/ContactMail";

import {
  Button,
  Toolbar,
  Box,
  Container,
  Typography,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  SwipeableDrawer,
  Grid,
} from "@mui/material";
import Bottom from "./Bottom";
import Contact from "./Contact";
import Services from "./Services";
import AboutUs from "./AboutUs";
import Home from "./Home";

export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pages: ["Home", "About Us", "Services", "Contact"],
      pagesLink: ["home", "about", "services", "contact"],
      pagesIcon: [
        <HomeIcon></HomeIcon>,
        <InfoIcon></InfoIcon>,
        <ComputerIcon></ComputerIcon>,
        <ContactMailIcon></ContactMailIcon>,
      ],
      openDrawer: false,
      isScrolled: true,
      showCompanyName: true,
      showIcon: true,
    };
    window.addEventListener("scroll", this.isScrolling);
    window.addEventListener("resize", this.showCompanyName);
    window.addEventListener("resize", this.showIcon);
  }

  isScrolling = () => {
    if (window.scrollY > 0) {
      this.setState({ isScrolled: true });
    } else {
      this.setState({ isScrolled: false });
    }
  };

  showIcon = () => {
    if (window.innerWidth < 230) {
      this.setState({ showIcon: false });
    } else {
      this.setState({ showIcon: true });
    }
  };

  showCompanyName = () => {
    if (window.innerWidth < 300) {
      this.setState({ showCompanyName: false });
    } else {
      this.setState({ showCompanyName: true });
    }
  };

  openDrawer = () => {};

  toggleDrawer = () => {
    this.setState({ openDrawer: !this.state.openDrawer });
  };

  render() {
    return (
      <React.Fragment>
        <AppBar
          position="fixed"
          style={{
            background: this.state.isScrolled
              ? "rgba(0, 0, 0, 0.8)"
              : "transparent",
            transition: "0.75s",
          }}
        >
          <Container maxWidth="xl">
            <Toolbar
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              {this.state.showCompanyName && (
                <Typography
                  style={{ fontSize: "min(4vw, 35px)" }}
                  component="a"
                  href="home"
                  sx={{
                    display: { xs: "flex", md: "flex" },
                    fontFamily: "monospace",
                    fontWeight: 1000,
                    letterSpacing: ".4rem",
                    color: "white",
                    textDecoration: "none",
                  }}
                >
                  DPGradient
                </Typography>
              )}

              <Box
                sx={{
                  display: { xs: "none", md: "flex" },
                }}
              >
                {this.state.pages.map((page) => (
                  <Button
                    key={page}
                    style={{ fontSize: "min(1.5vw, 20px)" }}
                    sx={{
                      my: 2,
                      mx: 2,
                      color: this.state.isScrolled ? "white" : "black",
                      display: "block",
                    }}
                    // href={this.state.pagesLink[this.state.pages.indexOf(page)]}
                    onClick={() => {
                      document
                        .getElementById(
                          this.state.pagesLink[this.state.pages.indexOf(page)]
                        )
                        .scrollIntoView({ behavior: "smooth" });
                    }}
                  >
                    {page}
                  </Button>
                ))}
              </Box>
              <IconButton
                onClick={this.toggleDrawer}
                sx={{
                  display: { md: "none" },
                }}
              >
                <MenuIcon
                  style={{ color: this.state.isScrolled ? "white" : "black" }}
                  fontSize="medium"
                />
              </IconButton>
            </Toolbar>
          </Container>
        </AppBar>
        <nav>
          <SwipeableDrawer
            anchor="top"
            open={this.state.openDrawer}
            onClose={this.toggleDrawer}
            onOpen={this.openDrawer}
          >
            <Box sx={{ background: "rgba(0, 0, 0, 1)" }}>
              <List>
                {this.state.pages.map((page) => (
                  <ListItem key={page}>
                    <ListItemButton
                      sx={{ textAlign: "center" }}
                      // href={
                      //   this.state.pagesLink[this.state.pages.indexOf(page)]
                      // }
                      onClick={() => {
                        document
                          .getElementById(
                            this.state.pagesLink[this.state.pages.indexOf(page)]
                          )
                          .scrollIntoView({ behavior: "smooth" });
                        this.toggleDrawer();
                      }}
                    >
                      {this.state.showIcon && (
                        <ListItemIcon sx={{ color: "white" }}>
                          {this.state.pagesIcon[this.state.pages.indexOf(page)]}
                        </ListItemIcon>
                      )}
                      <ListItemText
                        sx={{ color: "white" }}
                        primary={page}
                      ></ListItemText>
                    </ListItemButton>
                  </ListItem>
                ))}
              </List>
            </Box>
          </SwipeableDrawer>
        </nav>
        <nav>
          <Grid
            container
            spacing={0}
            style={{ background: "white" }}
            direction="column"
          >
            <Grid item id="home">
              <Home></Home>
            </Grid>

            <Grid item id="about">
              <AboutUs></AboutUs>
            </Grid>

            <Grid item id="services">
              <Services></Services>
            </Grid>

            <Grid item id="contact">
              <Contact></Contact>
            </Grid>

            <Grid item>
              <Bottom></Bottom>
            </Grid>
          </Grid>
        </nav>
      </React.Fragment>
    );
  }

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {}
  componentDidCatch(error, info) {}
  componentWillUnmount() {}
}
