import React, { Component } from "react";
import { Grid, Typography } from "@mui/material";

export default class Services extends Component {
  constructor(props) {
    super(props);
    this.state = {
      smallPadding: false,
      services: ["Service 1", "Service 2"],
      servicesContent: [
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Error voluptates atque eveniet distinctio animi enim nihil tempore commodi dolorum qui magnam culpa omnis maxime quasi harum, cupiditate voluptatem ipsum blanditiis?",
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Error voluptates atque eveniet distinctio animi enim nihil tempore commodi dolorum qui magnam culpa omnis maxime quasi harum, cupiditate voluptatem ipsum blanditiis?",
      ],
    };
    window.addEventListener("resize", this.checkScreenWidth);
  }

  checkScreenWidth = () => {
    if (window.innerWidth < 1000) {
      this.setState({ smallPadding: true });
    } else {
      this.setState({ smallPadding: false });
    }
  };

  render() {
    return (
      <React.Fragment>
        <Grid
          container
          spacing={0}
          sx={{
            width: "100vw",
            height: "auto",
            minWidth: "130px",
            paddingTop: this.state.smallPadding ? "15vw" : "100px",
            paddingBottom: this.state.smallPadding ? "20vw" : "150px",
            backgroundImage: `linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.5)), url('./blue_white_bg.png')`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
          justifyContent="center"
          alignItems="center"
        >
          {this.state.services.map((service) => (
            <Grid
              container
              direction="column"
              key={service}
              style={{ marginBottom: "10vw", width: "75vw" }}
            >
              <Grid
                item
                sx={{
                  marginBottom: this.state.smallPadding ? "3vw" : "55px",
                }}
              >
                <Typography
                  style={{
                    textAlign: "justify",
                  }}
                  sx={{
                    fontSize: "min(5vw, 35px)",
                    color: "white",
                    fontWeight: "bold",
                  }}
                >
                  {service}
                </Typography>
              </Grid>
              <Grid item>
                <Typography
                  style={{
                    textAlign: "justify",
                  }}
                  sx={{ fontSize: "min(3vw, 30px)", color: "white" }}
                >
                  {
                    this.state.servicesContent[
                      this.state.services.indexOf(service)
                    ]
                  }
                </Typography>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </React.Fragment>
    );
  }

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {}
  componentDidCatch(error, info) {}
  componentWillUnmount() {}
}
