import React, { Component } from "react";
import { Grid } from "@mui/material";

export default class Bottom extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <Grid container sx={{ width: "100vw", height: "auto" }}>
          <Grid item sx={{ width: "100vw", height: "auto" }}>
            <video
              src="./bottom_video.mp4"
              style={{
                width: "100vw",
                height: "auto",
                minWidth: "130px",
                display: "block",
              }}
              loop
              muted
              autoPlay
            ></video>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {}
  componentDidCatch(error, info) {}
  componentWillUnmount() {}
}
