import React, { Component } from "react";
import { Grid, IconButton, Typography } from "@mui/material";
import PlaceIcon from "@mui/icons-material/Place";
import EmailIcon from "@mui/icons-material/Email";

export default class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      smallPadding: false,
      contacts: ["Melbourne, Australia", "email@gmail.com"],
      contactsIcon: [
        <PlaceIcon sx={{ fontSize: "min(5vw, 75px)" }}></PlaceIcon>,
        <EmailIcon sx={{ fontSize: "min(5vw, 75px)" }}></EmailIcon>,
      ],
    };
    window.addEventListener("resize", this.checkScreenWidth);
  }

  checkScreenWidth = () => {
    if (window.innerWidth < 1000) {
      this.setState({ smallPadding: true });
    } else {
      this.setState({ smallPadding: false });
    }
  };

  render() {
    return (
      <React.Fragment>
        <Grid
          container
          spacing={0}
          justifyContent="center"
          alignItems="center"
          sx={{
            width: "100vw",
            height: "auto",
            minWidth: "130px",
            paddingTop: this.state.smallPadding ? "10vw" : "200px",
            // paddingTop: "10vw",
            paddingBottom: this.state.smallPadding ? "10vw" : "200px",
            // paddingBottom: "10vw",
            background: "black",
          }}
        >
          {this.state.contacts.map((contact) => (
            <Grid item key={contact}>
              <Grid
                container
                direction="column"
                style={{ marginBottom: "15px" }}
                sx={{
                  width: "50vw",
                  height: "auto",
                  minWidth: "130px",
                }}
              >
                <Grid item>
                  <div style={{ textAlign: "center" }}>
                    <IconButton
                      sx={{
                        color: "white",
                      }}
                    >
                      {
                        this.state.contactsIcon[
                          this.state.contacts.indexOf(contact)
                        ]
                      }
                    </IconButton>
                  </div>
                </Grid>

                <Grid item>
                  <Typography
                    style={{
                      textAlign: "center",
                    }}
                    sx={{
                      fontSize: "min(3vw, 30px)",
                      color: "white",
                    }}
                  >
                    {contact}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          ))}
        </Grid>
      </React.Fragment>
    );
  }

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {}
  componentDidCatch(error, info) {}
  componentWillUnmount() {}
}
